<template>
	<div class="bind">
		<div class="input-wrap">
			<div class="input-item">
				<label for="inpt-name">姓名</label>
				<input type="text" readonly :value="name" id="inpt-name"/>
			</div>
			<div class="input-item">
				<label for="inpt-sex">性别</label>
				<input type="text" readonly :value="sex" id="inpt-sex"/>
			</div>
			<div class="input-item">
				<label for="inpt-idNum">身份证号</label>
				<input type="text" readonly :value="idNum" id="inpt-idNum"/>
			</div>
			<div class="input-item">
				<label for="inpt-securityNumber">医保账号</label>
				<input type="text" v-model="securityNumber" id="inpt-securityNumber"/>
				<span class="close" @mouseup="clear('securityNumber')" v-if="securityNumber!==''"></span>
			</div>
			<div class="input-item">
				<label for="inpt-cardNum">医保卡号</label>
				<input type="text" v-model="cardNum" id="inpt-cardNum"/>
				<span class="close" @mouseup="clear('cardNum')" v-if="cardNum!==''"></span>
			</div>
		</div>
		<div class="tip">医保账号：社会保障卡上的保险号<br />医保卡号：社会保障卡上的卡号</div>
		<div class="cover" v-if="error">
			<div class="error">
				<div class="errorTxt">
					<span v-text="error"></span>
				</div>
				<div class="closeTip" @click="closeTip">确定</div>
			</div>
		</div>
		<div class="submit" :class="{isSubmit: isSubmit}" @click="submit">提交</div>
	</div>
</template>

<script>
import {mapGetters} from 'vuex';
export default {
	data(){
		return {
			name: '',
			idNum: '',
			securityNumber: '',
			cardNum: '',
			sex: '',
			error: ''
		}
	},
	computed: {
		isSubmit(){
			return this.securityNumber!=='' && this.cardNum!==''
		},
		...mapGetters(['userInfo']),
	},
	methods:{
		clear(key){
			this[key] = '';
		},
		closeTip(){
			this.error = '';
		},
		submit(){
			if(!this.isSubmit){return}
			this.$api.bind.bindSubmit({
				name: this.name,
				idNum: this.idNum,
				cardNum: this.cardNum,
				securityNumber: this.securityNumber,
				sex: this.sex
			}).then(res=>{
				console.log('submitres',res);
				if(res.code==1){
					this.$router.back();
				}else{
					this.error = res.message
				}
			})
		}
	},
	created(){
		this.name = this.userInfo.name;
		this.idNum = this.userInfo.id_num;
		this.sex = this.userInfo.gender==0 ? '男' : '女';
	}
}
</script>

<style lang="scss" scoped="scoped">
@import "@/assets/styles/mixin.scss";
.bind{
	.input-wrap{
		padding-left: pxToRem(32px);
		background: #fff;
		.input-item{
			position: relative;
			border-bottom: pxToRem(2px) solid #EDEDED;
			height: pxToRem(100px);
			color: #222;
			box-sizing: content-box;
			background: #fff;
			font-size: 0;
			label{
				line-height: pxToRem(100px);
				font-size: pxToRem(32px);
				display: inline-block;
				width: pxToRem(188px);
				text-align: left;
			}
			input{
				display: inline-block;
				height: pxToRem(100px);
				width: pxToRem(525px);
				padding: pxToRem(32px) 0;
				line-height: pxToRem(36px);
				font-size: pxToRem(32px);
				border: 0px;
				outline: none;
				box-sizing: border-box;
				margin: 0;
				background: transparent;
			}
		}
		.close{
			display: block;
			position: absolute;
			content: '';
			background-image: url('../../assets/images/input-close.png');
			background-size: 100%;
			background-position: center;
			background-repeat: no-repeat;
			z-index: 1;
			right: pxToRem(25px);
			top: 50%;
			margin-top: pxToRem(-22px);
			width: pxToRem(44px);
			height: pxToRem(44px);
		}
	}
	.tip{
		margin: pxToRem(17px) pxToRem(32px);
		line-height: pxToRem(36px);
		font-size: pxToRem(24px);
		color: #8E8E93;
	}
	.cover{
		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		z-index: 10000;
		background: rgba($color: #000000, $alpha: .5);
		.error{
			width: pxToRem(540px);
			height: auto;
			transform: translate3d(-50%,-50%,0);
			position: absolute;
			top: 50%;
			left: 50%;
			background: #fff;
			.errorTxt{
				text-align: center;
				line-height: pxToRem(181px);
				span{
					color: #DF3031;
					font-size: pxToRem(30px);
					vertical-align: middle;
					line-height: pxToRem(39px);
				}
			}
			.closeTip{
				line-height: pxToRem(90px);
				font-size: pxToRem(30px);
				color: #2B509B;
				text-align: center;
				border-top: pxToRem(2px) solid #E1E1E1;
			}
		}
	}
	.submit{
		line-height: pxToRem(88px);
		width: pxToRem(654px);
		margin: pxToRem(15px) auto;
		color: #fff;
		border-radius: pxToRem(3px);
		text-align: center;
		background: rgba($color:  #DF3031, $alpha: .5);
		&.isSubmit{
			background: #DF3031;
		}
	}
}
</style>
